import React, {Component} from 'react'
import history from "./history"
import './Pmain.sass'
import {Router, Switch, Route, Link} from "react-router-dom";
// import Header from './../Header/Header'
import Menu from './../Components/Menu/Menu'
import Home from './../Pages/Home/Home'
import Teach from './../Pages/Teach/Teach'
import Products from './../Pages/Products/Products'
import Contact from './../Pages/Contact/Contact'
import About from './../Pages/About/About'
import Notfound from './../Pages/Notfound/Notfound'

export default class Pmain extends Component {
    render() {
        const maxWidth = "1200px"
        return (
            <Router history={history} className="Pmain">
                <div className="Pmain">
                    {/* <Header maxWidth={maxWidth}/> */}
                    <Menu maxWidth={maxWidth}/>
                    <Switch>
                    <Route exact path="/">
                            <Home maxWidth={maxWidth}/>
                        </Route>
                        <Route exact path="/teach">
                            <Teach maxWidth={maxWidth}/>
                        </Route>
                        <Route exact path="/products">
                            <Products maxWidth={maxWidth}/>
                        </Route>
                        <Route exact path="/contact">
                            <Contact maxWidth={maxWidth}/>
                        </Route>
                        <Route exact path="/about">
                            <About maxWidth={maxWidth}/>
                        </Route>
                        <Route>
                            <Notfound maxWidth={maxWidth}/>
                        </Route>
                    </Switch>
                </div>
            </Router>
        )
    };
};
