import React, {Component} from 'react'
import './Article_card.sass'
import {Link} from 'react-router-dom'

export default class Article_card extends Component {
    render() {
        const unixTime = this.props.data.date;
        const date = new Date(unixTime*1000);
        return (
            
            <li className="acrticle_card">
                <div>
                    <div>
                        <Link className="link_img" to={this.props.data.link}><img className="img" src={this.props.data.img}/></Link>
                        <div>
                            <Link to={this.props.data.category_link} className="category">{this.props.data.category}</Link>
                            <Link to={this.props.data.link} className="title">{this.props.data.title}</Link>
                            <span className="date_time">{date.toLocaleString("fa-IR")}</span>
                            <span className="subtitle">{this.props.data.subtitle}</span>
                        </div>
                    </div>
                </div>
            </li>
        )
    }
}
